.loader{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    
}

span{
    width: 10px;
    height: 10px;
    margin: 0 5px;
    border-radius: 50%;
}

span:first-child{
    
    animation: firstSpan 2s 1s infinite;
    
}
span:nth-child(2){
    animation: twoSpan 2s 1.5s infinite;
}
span:last-child{
    animation: lastSpan 2s 1s infinite;
}
    

@keyframes firstSpan {
    0%{
        background-color: rgba(71, 147, 255, 1);
    }
    50%{
        background-color: rgba(71, 147, 255, 0.66);
    }
    100%{
        background-color: rgba(71, 147, 255, 0.33);
    }
}
@keyframes twoSpan {
    0%{
        background-color: rgba(71, 147, 255, 0.33);
    }
    50%{
        background-color: rgba(71, 147, 255, 1);
    }
    100%{
        background-color: rgba(71, 147, 255, 0.33);
    }
}
@keyframes lastSpan {
    0%{
        background-color: rgba(71, 147, 255, 0.33);
    }
    50%{
        background-color: rgba(71, 147, 255, 0.33);
    }
    100%{
        background-color: rgba(71, 147, 255, 1);
    }
}